<template>
<div class="row">
    <div class="txt--contactenos">
        <div>
            <h2>Â¡Contactenos!</h2>
        </div>
    </div>
    <div class="dgrid-contactenos">
        <div class="container-img">
            <img src="../assets/img--bg-contact.svg" />
        </div>
        <div class="contact__form">
            <div class="form-group">
                <input id="nombre" class="form-control" type="text" placeholder="Nombre" />
            </div>
            <div class="form-group">
                <input id="correo" v-model="mail" class="form-control" type="email" placeholder="correo" />
            </div>
            <div class="form-group">
                <TextArea id="mensaje" class="form-control" type="text" cols="1" rows="4" placeholder="asunto" />
            </div>
            <div class="form-group">
                <button class="form-control" :disabled="isDisabled" v-on:click="enviarMensaje">
                    Enviar
                </button>
            </div>
        </div>
    </div>
</div>
</template>

<script>
const URL = "https://rest-mail-portafolio.herokuapp.com/api/mail" //"http://localhost:9890/api/mail";
var regmail = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;

export default {
    data: function () {
        return {

            mail: ""
        }
    },
    computed: {
        isDisabled: function () {

            if (this.mail.length > 8) {
                return false;
            } else {
                return true
            }
        }
    },
    methods: {
        enviarMensaje: function () {
            console.log("click")
            if (regmail.test(document.getElementById("correo").value)) {
                fetch(URL, {
                        method: "POST",
                        mode: "cors",
                        headers: {
                            "Content-Type": "application/json",
                        },
                        body: JSON.stringify({
                            nombre: document.getElementById("nombre").value,
                            correo: document.getElementById("correo").value,
                            mensaje: document.getElementById("mensaje").value,
                        }),
                    })
                    .then((response) => response.json())
                    .then((data) => {
                        // console.log("data")

                        alert(data.message);
                        this.clear();
                    })
                    .catch((err) => console.log(err.message));
            }
        },
        clear: () => {
            document.getElementById("nombre").value = "";
            document.getElementById("correo").value = "";
            document.getElementById("mensaje").value = "";
            // this.mail = "";
        },
    },
};
</script>

<style scoped>
.dgrid-contactenos {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-gap: 50px;
    margin: 20px 0;
    width: 100%;
}

.contact__form {
    width: 100%;
}

.txt--contactenos {
    width: 100%;
    margin: 2rem 0;
}

.txt--contactenos h2 {
    text-align: center;
}

.contact__form TextArea {
    resize: none;
}

.contact__form button {
    border-radius: 2rem;
    background: skyblue;
    color: #ffffff;
    font-weight: 600;
}

.contact__form button:hover {
    background: slateblue;
    transition: 0.5s all ease;
}

@media (min-width: 350px) and (max-width: 599px) {
    .container-img {
        display: none;
    }

    .contact__form {
        grid-column: 1/-1;
    }

    .dgrid-contactenos {
        margin: 20px;
    }
}
</style>
