<template>
<nav class="navbar navbar-expand-lg navbar-light bg-light menuFixed">
    <a class="navbar-brand" href="#">TEST PE</a>
    <button id="btnBurger" class="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
        <span class="navbar-toggler-icon"></span>
    </button>
    <div class="collapse navbar-collapse" id="navbarSupportedContent">
        <ul id="list-menu" v-on:click="agregarActive()" class="navbar-nav d-flex ml-auto">
            <li class="nav-item">
                <a class="nav-link" href="#Home">
                    <!-- <router-link to="/">Home</router-link>-->
                    Home
                </a>
            </li>
            <li class="nav-item">
                <a class="nav-link" href="#Servicios">
                    <!--<router-link to="/service">Service</router-link>-->
                    Servicios

                </a>
            </li>
            <li class="nav-item">
                <a class="nav-link" href="#Contacto">
                    <!--<router-link to="/about">Contacto</router-link>-->
                    Contacto
                </a>
            </li>
        </ul>
    </div>
</nav>
</template>

<script>
export default {

    methods: {
        agregarActive: () => {
            var ul = document.getElementById("list-menu");
            var as = document.querySelector("ul").getElementsByTagName("a");
            var loction = location.hash.split("#")[1] //pathname.split("/")[1];
            if (loction == "") return;
            // debugger;

            $('li').on('click', function (e) {
                $(this).parent().find('li.active').removeClass('active');
                $(this).addClass('active');
                if (screen.availWidth < 600) {
                    //remove expanded
                    if (document.getElementById("btnBurger").getAttribute("aria-expanded")) {
                        document.getElementById("btnBurger").setAttribute("aria-expanded", false)
                        document.getElementById("btnBurger").classList.add("collapsed")
                        document.getElementById("navbarSupportedContent").classList.remove("show")

                    }
                }
            });
        }
    }
}
</script>

<style scoped>
.menuFixed {
    position: fixed;
    z-index: 10;
    width: 100%;
}

@media (min-width: 992) {
    .navbar-expand-lg .lstMenu {
        justify-content: flex-end !important;
        width: 100%;
        background: red;
    }
}
</style>
